body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.column {
  float: left;
  width: 33.3%;
  margin-bottom: 16px;
  margin-left: 150px;
  padding: 0 8px;
  align-items: center;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  margin: 8px;
  align-content: center;
}

.about-section {
  padding: 50px;
  text-align: center;
  background-color: rgba(43, 43, 43);
  color: rgb(255, 217, 0);
}

.container {
  padding: 0 16px;
}

/*.container::after,
.row::after {
  content: "";
  clear: both;
  display: table;
} */

.title {
  color: black;
}

.button {
  border: none;
  outline: 0;
  display: inline-block;
  padding: 8px;
  color: white;
  background-color: #000;
  text-align: center;
  cursor: pointer;
  width: 100%;
}

.button:hover {
  background-color: #555;
}

@media screen and (max-width: 650px) {
  .column {
    width: 100%;
    display: block;
  }
}

.OurTeam {
  text-align: center;
}

.profile {
  width: 100%;
}
